import React, { Component } from 'react'
import {Link,Route} from 'react-router-dom';
import Job from './Job';
import Test from './Test';
import Slider from './Slider';

export default class Home extends React.Component {
    render() {
        let {url} = this.props.match;
        return (
            <div>
                <div className="left-header">
                    <Link to={`${url}/all/1`}>全部</Link>
                    <Link to={`${url}/good/1`}>精华</Link>
                    <Link to={`${url}/share/1`}>分享</Link>
                    <Link to={`${url}/ask/1`}>问答</Link>
                    <Link to={`${url}/job`}>招聘</Link>
                    <Link to={`${url}/test`}>客户端测试</Link>
                </div>
                <div className="panel">
                    <Route path={url+'/all'} component={Slider}></Route>
                    <Route path={url+'/good'} component={Slider}></Route>
                    <Route path={url+'/share'} component={Slider}></Route>
                    <Route path={url+'/ask'} component={Slider}></Route>
                    <Route path={url+'/job'} component={Job}></Route>
                    <Route path={url+'/test'} component={Test}></Route>
                    <div>
                        <div>
                            
                        </div>
                    </div>
                </div>
                
                
            </div>
        )
    }
}
