import React, { Component } from 'react';
import {Link,Route} from 'react-router-dom';
import Content from './Content'
export default class Slider extends React.Component {
    render(){
        let {url} = this.props.match;
        return (
            <div >
                <Route path='/home/:tab/:page' component={Content}></Route>
                <ul className="list">
                        <li><Link >«</Link></li>
                        <li><Link to={`${url}/1`}>1</Link></li>
                        <li><Link to={`${url}/2`}>2</Link></li>
                        <li><Link to={`${url}/3`}>3</Link></li>
                        <li><Link to={`${url}/4`}>4</Link></li>
                        <li><Link to={`${url}/5`}>5</Link></li>
                        <li><Link to={`${url}/1`}>6</Link></li>
                        <li><Link to={`${url}/2`}>7</Link></li>
                        <li><Link to={`${url}/3`}>8</Link></li>
                        <li><Link to={`${url}/4`}>9</Link></li>
                        <li><Link to={`${url}/5`}>10</Link></li>
                        <li><Link >…</Link></li>
                        <li><Link >»</Link></li>
                    </ul>
            </div>
        )
    }
    
}
